
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import logo from "./assets/logo.png";
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';

import React, { useCallback, useEffect, useState } from "react";
import web3ModalSetup from './helpers/web3ModalSetup';
import Web3 from "web3";
import getAbi from './ABI/miner';
import getAbiBusd from './ABI/busd';
import IconButton from "@material-ui/core/IconButton";
import Snackbar from "@material-ui/core/Snackbar";
import CloseIcon from "@material-ui/icons/Close";

const web3Modal = web3ModalSetup();
function App() {
  const [Abi, setAbi] = useState();
  const [AbiBusd, setAbiBusd] = useState();
  const [web3, setWeb3] = useState();
  const [isConnected, setIsConnected] = useState(false);
  const [injectedProvider, setInjectedProvider] = useState();
  const [refetch, setRefetch] = useState(true);
  const [errorMessage, setErrorMessage] = useState(null);
  const [accounts, setAccounts] = useState(null);
  const [current, setCurrent] = useState(null);
  const [connButtonText, setConnButtonText] = useState("CONNECT");
  const [open, setOpen] = React.useState(false);
  const [message,setMessage] = useState('');
  const [userName,setuserName] = useState('');
  const [slot,setSlot] = useState(0);

  const handleToClose = (event, reason) => {
    if ("clickaway" === reason) return;
    setOpen(false);
  };

  const logoutOfWeb3Modal = async () => {
    await web3Modal.clearCachedProvider();
    if (
      injectedProvider &&
      injectedProvider.provider &&
      typeof injectedProvider.provider.disconnect == "function"
    ) {
      await injectedProvider.provider.disconnect();
    }
    setIsConnected(false);

    window.location.reload();
  };
  const loadWeb3Modal = useCallback(async () => {
    const provider = await web3Modal.connect();
    setInjectedProvider(new Web3(provider));
    const acc = provider.selectedAddress
      ? provider.selectedAddress
      : provider.accounts[0];


    const short = shortenAddr(acc);

    setWeb3(new Web3(provider));
    setAbi(await getAbi(new Web3(provider)));
    setAbiBusd(await getAbiBusd(new Web3(provider)));
    setAccounts([acc]);
    setCurrent(acc);
    //     setShorttened(short);
    setIsConnected(true);

    setConnButtonText(short);

    provider.on("chainChanged", (chainId) => {
      console.log(`chain changed to ${chainId}! updating providers`);
      setInjectedProvider(new Web3(provider));
    });

    provider.on("accountsChanged", () => {
      console.log(`account changed!`);
      setInjectedProvider(new Web3(provider));
    });

    // Subscribe to session disconnection
    provider.on("disconnect", (code, reason) => {
      console.log(code, reason);
      logoutOfWeb3Modal();
    });
    // eslint-disable-next-line
  }, [setInjectedProvider]);

  useEffect(() => {
    setInterval(() => {
      setRefetch((prevRefetch) => {
        return !prevRefetch;
      });
    }, 10000);
  }, []);

  useEffect(() => {
    if (web3Modal.cachedProvider) {
      loadWeb3Modal();
    }
    // eslint-disable-next-line
  }, []);

  const shortenAddr = (addr) => {
    if (!addr) return "";
    const first = addr.substr(0, 3);
    const last = addr.substr(38, 41);
    return first + "..." + last;
  };

  const Statistics = async () => {
    if (isConnected && Abi) {

   let slot = await Abi.methods.slot().call();
   setSlot(slot);



    }
  }

  useEffect(() => {
    Statistics();
    // eslint-disable-next-line
  }, [isConnected, web3, Abi, refetch]);



  const Approve = async (e) => {
    // leave as it
     e.preventDefault();
     if (isConnected && AbiBusd) {
      setOpen(true);
       setMessage("Approving Busd");
      let _contract = '0x2E06b2359E156ac03B87dB8Ed93FA39bE2Ca8683';
      let _value = await web3.utils.toWei('1500');
        await AbiBusd.methods.approve(_contract,_value).send({
              from: current,
        });
        setOpen(true);
        setMessage("Approved Successfully");
   
     }
     else {
      setOpen(true);
      setMessage("Connect Wallet");
     }
     Statistics();
 
   }
  
   const Deposit = async (e) => {
    // leave as it
     e.preventDefault();
     if (isConnected && Abi) {
      setOpen(true);
       setMessage("Depositing BUSD");
      let _value = await web3.utils.toWei('1500');
        await Abi.methods.buySlot(_value,userName).send({
              from: current,
        });
        setOpen(true);
        setMessage("Deposited Successfully");
   
     }
     else {
      setOpen(true);
      setMessage("Connect Wallet");
     }
     Statistics();
   }

  return (
<>
<Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
      <Container>
        <Navbar.Brand href="#home"><img src={logo} alt="logo" width={70} /></Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link href="https://trade.enginebusd.io">TradeBank</Nav.Link>
            <Nav.Link href="https://app.enginebusd.io">Mini-Miner</Nav.Link>

     
          </Nav>
          <Nav>
       
            <Nav.Link>
              <button className='btn btn-warning btn-lg' onClick={loadWeb3Modal}>{connButtonText}</button>
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>

<center>
<img src={logo} alt="logo" />
<h2>EYESWAP - BOT - PRIVATE SALE</h2>
</center>
<div className='row'>
  <div className='col-sm-2'></div>
  <div className='col-sm-8'>

  <div className='card' style={{background:"none",color:"#fff",fontWeight:"bold"}}>
  <div className='card-header'>
    PRIVATE SALE
  </div>
  <div className='card-body'>
    <table className='table'>
      <tbody>
      <tr>
        <td>SOLD SLOTS</td>
        <td>{slot}/10</td>

      </tr>
      <tr>
        <td>MAX SLOTS</td>
        <td>10</td>

      </tr>

      <tr>
        <td>PRICE</td>
        <td>1500 BUSD</td>

      </tr>

     
      </tbody>
    </table>
    <p><b>YOUR TELEGRAM</b></p>
    <input type="text" placeholder="Telegram Username" className="form-control" 
     value={userName}
     onChange={(e) => setuserName(e.target.value)}
    /><br />
    <button className='btn btn-warning'
    style={{border:"1px solid #333"}}
    onClick={Approve}
    >APPROVE</button> <button className='btn btn-warning'
    style={{border:"1px solid #333"}}
    onClick={Deposit}
    >DEPOSIT</button>


  
  </div>
</div>

<br />
<center>
<h2>Advantages</h2>
<p>- 24/7 Support <br /> - 0% Trading Fee <br /> - Provide Upcoming bots private <br /> - Node Installation <br /> - Coins Signals <br />
- Maintaince <br /> - Complete Guideline <br /> 
</p>

<br />
<h3><a href="https://eyeswap.money" style={{textDecoration:"none",color:"black"}}>WEBSITE</a> || <a href="https://t.me/enginebusd"  style={{textDecoration:"none",color:"black"}}> TELEGRAM </a> || <a href="https://t.me/enginebusd"  style={{textDecoration:"none",color:"black"}}>TWITTER</a></h3></center>
  
<Snackbar
        anchorOrigin={{
          horizontal: "left",
          vertical: "bottom",
        }}
        open={open}
        autoHideDuration={5000}
        message={message}
        onClose={handleToClose}
        action={
          <React.Fragment>
            <IconButton
              size="small"
              aria-label="close"
              color="inherit"
              onClick={handleToClose}
            >
              <CloseIcon fontSize="small" />
            </IconButton>
          </React.Fragment>
        }
        />
  
  </div>
  <div className='col-sm-2'></div>
</div>





</>
  );
}

export default App;
